import React, { useState } from 'react';
import { Transaction, SystemProgram, LAMPORTS_PER_SOL, Connection, PublicKey } from '@solana/web3.js';

function App() {
  //       
  const [walletAddress, setWalletAddress] = useState(null);

  //    Phantom
  const connectWallet = async () => {
    if (window?.solana && window.solana.isPhantom) {
      try {
        const resp = await window.solana.connect();
        setWalletAddress(resp.publicKey.toString());
      } catch (err) {
        console.error(' :', err);
      }
    } else {
      alert("Phantom wallet  . ,  Phantom.");
    }
  };

  //    Phantom
  const disconnectWallet = async () => {
    if (window?.solana && window.solana.isPhantom) {
      try {
        await window.solana.disconnect();
        setWalletAddress(null);
      } catch (err) {
        console.error('   :', err);
      }
    }
  };

  //   0.001 SOL   
  const transferSol = async () => {
    if (!walletAddress) {
      alert("    .");
      return;
    }
    try {
      //    devnet
      const connection = new Connection("https://api.devnet.solana.com");
      //  
      const recipient = new PublicKey("3HE6EtGGxMRBuqqhz2gSs3TDRXebSc8HDDikZd1FYyJj");
      //  
      const transaction = new Transaction().add(
        SystemProgram.transfer({
          fromPubkey: window.solana.publicKey,
          toPubkey: recipient,
          lamports: 0.001 * LAMPORTS_PER_SOL, // 0.001 SOL  
        })
      );

      transaction.feePayer = window.solana.publicKey;
      const { blockhash } = await connection.getRecentBlockhash();
      transaction.recentBlockhash = blockhash;

      //  
      const signedTransaction = await window.solana.signTransaction(transaction);
      //    
      const signature = await connection.sendRawTransaction(signedTransaction.serialize());
      alert(" . TXID: " + signature);
    } catch (err) {
      console.error("   SOL:", err);
      alert("   SOL");
    }
  };

  return (
    <div style={{
      backgroundColor: 'black',
      height: '100vh',
      color: 'white',
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center'
    }}>
      <h1>Hello World</h1>
      {walletAddress ? (
        <>
          <p>: {walletAddress}</p>
          <button 
            onClick={transferSol}
            style={{
              padding: '10px 20px',
              fontSize: '16px',
              cursor: 'pointer',
              borderRadius: '5px',
              border: 'none',
              marginTop: '10px'
            }}
          >
             0.001 SOL
          </button>
          <button 
            onClick={disconnectWallet} 
            style={{
              padding: '10px 20px', 
              fontSize: '16px', 
              cursor: 'pointer',
              borderRadius: '5px',
              border: 'none',
              marginTop: '10px'
            }}
          >
             Phantom Wallet
          </button>
        </>
      ) : (
        <button 
          onClick={connectWallet} 
          style={{
            padding: '10px 20px', 
            fontSize: '16px', 
            cursor: 'pointer',
            borderRadius: '5px',
            border: 'none'
          }}
        >
           Phantom Wallet
        </button>
      )}
    </div>
  );
}

export default App;
